body {
  background-color: #efefef;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

nav {
  background-color: #fff;
  text-align: center;
  padding: 20px;
  -webkit-box-shadow: 0 2px 2px 0 rgba(0,0,0,.15);
          box-shadow: 0 2px 2px 0 rgba(0,0,0,.15);
}

nav h2 {
  color: #343a40;
  letter-spacing: 2px;
  margin: 0 auto;
}

input[type=range]{
  width: 100%;
  -webkit-appearance: none;
}

input[type=range]::-webkit-slider-runnable-track {
  background: #ddd;
  border: none;
  border-radius: 3px;
  height: 5px;
}

input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: none;
  height: 16px;
  width: 16px;
  border-radius: 20%;
  background: goldenrod;
  margin-top: -4px;
}

input[type=range]:focus {
  outline: none;
}

input[type=range]:focus::-webkit-slider-runnable-track {
    background: #ccc;
}

.area-blue {
  fill: lightsteelblue;
}

.area-green {
  fill: lightgreen;
}

.axis {
  font: 10px sans-serif;
}

.axis path,
.axis line {
  fill: none;
  stroke: #000;
  shape-rendering: crispEdges;
}

.bar {
  fill: #35978f;
}

.bar:hover {
  fill: #8c510a;
}

.center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.grid {
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
  -ms-flex-wrap: wrap;
}

.grid-box {
  background-color: #fff;
  -webkit-box-shadow: 0 0 4px 2px rgba(0,0,0,.1);
          box-shadow: 0 0 4px 2px rgba(0,0,0,.1);
  margin: 20px;
}

.grid-box .grid-header {
  background-color: #fdfbf8;
  border-bottom: 1px solid #d8d8d8;
  padding: 10px;
}

.grid-box .grid-header h4 {
  margin: 0 auto;
}

.grid-cell {
  flex-basis: 0;
  flex-grow: 1;
  max-width: 100%;
  -ms-flex-preferred-size: 0;
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
}

.line-blue {
  fill: none;
  stroke: steelblue;
  stroke-width: 2px;
}

.line-red {
  fill: none;
  stroke: red;
  stroke-width: 2px;
}

.line-green {
  fill: none;
  stroke: green;
  stroke-width: 2px;
}

.slider .title {
  font-size: 14px;
  margin-bottom: 20px;
  color: #868e96;
  letter-spacing: 2px;
}

.slider .slider-value {
  margin: 20px auto;
  border: 1px solid #dee2e6;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 8px;
  border-radius: 4px;
}

.slider-container {
  border: 1px solid #d8d8d8;
  padding: 40px;
  margin: 5px;
}

.tick line {
  stroke: rgba(0, 0, 0, 0.1);
}



